import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import Modal from "./Modal";
import config from "../config";
import { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

function Navbar() {
  const navigate = useNavigate();
  const [memberName, setMemberName] = useState("");
  const handleSingOut = () => {
    Swal.fire({
      title: "Sing out",
      text: "Confirm Sing out",
      icon: "question",
      showConfirmButton: true,
      showCancelButton: true,
    }).then((res) => {
      if (res.isConfirmed) {
        localStorage.removeItem(config.token_name);
        navigate("/login");
      }
    });
  };

  const handleEditProfile = async () => {
    try {
      await axios
        .get(config.path_api + "/member/info", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setMemberName(res.data.result.name);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.file({
        title: "error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleChangeSave = async () => {
    try {
      const payload = { memberName: memberName };
      await axios
        .put(
          config.path_api + "/member/changeProfile",
          payload,
          config.headers()
        )
        .then((res) => {
          if (res.data.message === "success") {
            document.getElementById("btnModalClose").click();
            Swal.fire({
              icon: "success",
              title: "Success",
              text: "ทำการเปลี่ยนชื่อร้านสำเร็จ",
              showConfirmButton: false,
              timer: 2000,
            }).then(() => {
              window.location.reload();
            });
          } 
        });
    } catch (e) {
      if (e.response.data.message === "Duplicate" &&
      e.response.status === 400 
      ) {
        Swal.fire({
          icon: "info",
          title: "ชื่อร้านค้าซ้ำ",
          showConfirmButton: true,  
        })
      } else {
        Swal.fire({
          icon: "error",
          title: "error",
          text: e.message,
        });
      }
    }
  };

  return (
    <>
      <nav className="main-header navbar navbar-expand navbar-white navbar-light">
        <ul className="navbar-nav">
          <li className="nav-item">
            <Link
              className="nav-link"
              data-widget="pushmenu"
              to="#"
              role="button"
            >
              <i className="fas fa-bars"></i>
            </Link>
          </li>
        </ul>

        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <button
              onClick={handleEditProfile}
              className="btn btn-info mr-2"
              data-bs-toggle="modal"
              data-bs-target="#EditProfileModal"
            >
              <i className="fa fa-user mr-2"></i>
              Profile
            </button>
            <button onClick={handleSingOut} className="btn btn-danger mr-2">
              <i className="fa fa-times mr-2"></i>
              Sing Out
            </button>
          </li>
        </ul>
      </nav>

      <Modal id="EditProfileModal" title="แก้ไขชื่อร้าน">
        <div className="row g-2">
          <div className="col-md-12">
            <label htmlFor="editProfile" className="form-label">
              ชื่อร้าน
            </label>
            <input
              value={memberName}
              onChange={(e) => setMemberName(e.target.value)}
              className="form-control"
              type="text"
            />
          </div>

          <div className="col-md-12 d-block text-start">
            <button
              onClick={handleChangeSave}
              
              className="btn btn-primary btn-sm"
            >
              <i className="fa fa-check"></i> Save
            </button>
          </div>
        </div>
      </Modal>
    </>
  );
}
export default Navbar;
