import { useState, useEffect } from "react";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

function Packages() {
  const [packages, setPackage] = useState([]);
  const [yourPackage, setYourPackage] = useState({});
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [pass, setPass] = useState("");

  const navgate = useNavigate();

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      axios
        .get(config.path_api + "/package/list")
        .then((res) => {
          setPackage(res.data.results);
        })
        .catch((err) => {
          throw err.response.message;
        });
    } catch (e) {
      console.log(e.message);
    }
  };

  const ChoosePackage = (item) => {
    setYourPackage(item);
  };

  const handleRegister = async (e) => {
    e.preventDefault();
    try {
      let checkTrue = true;
      if (name.length <= 0) {
        checkTrue = false;
        Swal.fire("กรุณากรอกชื่อร้าน", "", "question");
      } else if (phone.length <= 0) {
        checkTrue = false;
        Swal.fire("กรุณากรอกเบอร์โทร", "", "question");
      } else if (pass.length <= 0) {
        checkTrue = false;
        Swal.fire("กรุณาตั้งรหัสผ่าน", "", "question");
      }

      if (checkTrue) {
        Swal.fire({
          icon: "info",
          text: "Confirm Register Pachage:" + yourPackage.id,
          showConfirmButton: true,
          showCancelButton: true,
        }).then((res) => {
          if (res.isConfirmed) {
            const payload = {
              packageID: yourPackage.id,
              name: name,
              phone: phone,
              pass: pass,
            };
            axios
              .post(config.path_api + "/package/register@package", payload)
              .then((res) => {
                if (res.data.message === "success") {
                  document.getElementById("btnModalClose").click();
                  Swal.fire("You Confirm!", "success").then(() => {
                    setPhone("");
                    setPass("");
                    navgate("/login");
                  });
                }
              })
              .catch((e) => {
                if (
                  e.response.data.message === "Duplicate" &&
                  e.response.status === 400
                ) {
                  Swal.fire({
                    icon: "info",
                    title: "ชื่อร้านค้า หรือ เบอร์โทร ซ้ำ",
                    showConfirmButton: true,
                  });
                } else {
                  Swal.fire({
                    title: "error",
                    icon: "error",
                    text: e.message,
                  });
                }
              });
          }
        });
      }
    } catch (e) {
      Swal.fire({
        icon: "error",
        title: "Not found",
        text: e.message,
      });
    }
  };
  return (
    <>
      <div className=" container py-5">
        <div className="h1 display-2 fw-bold text-danger">Package Cloud</div>
        <div className="row g-2">
          {packages.map((item) => (
            <div key={item.id} className="col-md-4">
              <div className="card h-100 shadow text-center">
                <div className="card-body">
                  <div className="h1 display-5 fw-bold text-bg-danger rounded">
                    {item.name}
                  </div>
                  <div className="h5 mt-3 text-muted">
                    {parseInt(item.bill_amount).toLocaleString("th-TH")}
                    <span className="ms-2">บิลต่อเดือน</span>
                  </div>
                  <div className="h6 mb-3 text-muted">
                    <span className="me-2">ราคา</span>{" "}
                    {parseInt(item.price).toLocaleString("th-Th")}
                    <span className="ms-2">บาท/เดือน</span>
                  </div>
                  <div className="mt-3">
                    <button
                      onClick={(e) => ChoosePackage(item)}
                      className="btn btn-primary w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#RegisterModal"
                    >
                      Register
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="mt-5">
          <Link to="/login" className="btn btn-primary w-100">
            <i className="fa fa-check me-2"></i>
            Login
          </Link>
        </div>
      </div>
      <Modal id="RegisterModal" title="สมัครใช้บริการแพ็คเกจคลาว">
        <form onSubmit={handleRegister} className="row g-2 was-validated">
          <div className="col-md-12">
            <div className=" alert alert-primary">
              <span className="fw-bold">Package: {yourPackage.name} :</span>
              ราคา {parseInt(yourPackage.price).toLocaleString("th-TH")} บาท
            </div>
          </div>
          <div className="col-md-12">
            <label htmlFor="shopname" className="form-label">
              ชื่อร้าน
            </label>
            <input
              value={name}
              onChange={(e) => setName(e.target.value)}
              type="text"
              id="shopname"
              className="form-control"
              required
            />
            <div className="invalid-feedback">กรอกชื่อร้าน</div>
          </div>
          <div className="col-md-12">
            <label htmlFor="shopphone" className="form-label">
              เบอร์โทรติดตต่อ
            </label>
            <input
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              type="text"
              id="shopphone"
              className="form-control"
              required
            />
            <div className="invalid-feedback">กรอกเบอร์โทรที่ติดต่อได้</div>
          </div>
          <div className="col-md-12">
            <label htmlFor="shopPass" className="form-label">
              ตั้งรหัสผ่านของท่าน
            </label>
            <input
              value={pass}
              onChange={(e) => setPass(e.target.value)}
              type="password"
              id="shopPass"
              className="form-control"
              required
            />
            <div className="invalid-feedback">กรุณาตั้งรหัสผ่าน</div>
          </div>
          <div className="col-md-12 d-block text-start">
            <button
              type="submit"
              className="btn btn-primary btn-sm"
              onClick={handleRegister}
            >
              Confirm Register <i className="fa-solid fa-arrow-right"></i>
            </button>
          </div>
        </form>
      </Modal>
    </>
  );
}

export default Packages;
