import { useState, useEffect } from "react";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import Swal from "sweetalert2";
import Template from "../components/Template";
import { useNavigate } from "react-router-dom";

function Product() {
  const [product, setProduct] = useState({
    barcode: "",
    name: "",
    price: "",
    cost: "",
    detail: "",
  });
  const [products, setProducts] = useState([]);
  const [productImage, setProductImage] = useState({});
  const [productImages, setProductImages] = useState([]);

  const navgate = useNavigate();

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      await axios
        .get(config.path_api + "/product/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setProducts(res.data.result);
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error Product",
        text: e.message,
        icon: "error",
        showConfirmButton: false,
        timer: 1000,
      }).then(() => {
        navgate("/login");
      });
    }
  };

  const fetchDataProductImage = async (item) => {
    try {
      await axios
        .get(
          config.path_api + "/productImage/list/" + item.id,
          config.headers()
        )
        .then((res) => {
          if (res.data.message === "success") {
            setProductImages(res.data.result);
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleAddProduct = async (e) => {
    e.preventDefault();
    try {
      let url = config.path_api + "/product/insert";

      if (product.id !== undefined) {
        url = config.path_api + "/product/update";
      }

      await axios.post(url, product, config.headers()).then((res) => {
        if (res.data.message === "success") {
          Swal.fire({
            title: "Save Product Data",
            text: "Save Product Data Success",
            icon: "success",
            showConfirmButton: true,
          }).then(() => {
            fetchData();
            handleClose();
          });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleClose = () => {
    const btns = document.getElementsByClassName("btnClose");
    for (let i = 0; i < btns.length; i++) {
      btns[i].click();
    }
  };

  const handleDelete = (item) => {
    Swal.fire({
      title: "Delete Data",
      text: "Confirm Delete Data System Out",
      icon: "question",
      showCancelButton: true,
      showConfirmButton: true,
      allowOutsideClick: false,
      allowEscapeKey: false,
    }).then(async (res) => {
      if (res.isConfirmed) {
        try {
          await axios.delete(
            config.path_api + "/product/delete/" + item.id,
            config.headers()
          );
          fetchData();
          Swal.fire({
            title: "Delete Data",
            text: "Delete Data Success",
            icon: "success",
            timer: 2000,
          });
        } catch (e) {
          Swal.fire({
            title: "Error",
            text: e.message,
            icon: "error",
          });
        }
      }
    });
  };

  const handleClear = () => {
    setProduct({
      barcode: "",
      name: "",
      price: "",
      cost: "",
      detail: "",
    });
  };

  // const handleUpload = () => {
  //   Swal.fire({
  //     title: "Upload Image",
  //     text: "Confirm Upload Image",
  //     icon: "question",
  //     showCancelButton: true,
  //     showConfirmButton: true,
  //   }).then(async (res) => {
  //     if (res.isConfirmed) {
  //       try {
  //         const _config = {
  //           headers: {
  //             Authorization:
  //               "Bearer " + localStorage.getItem(config.token_name),
  //             "Content-Type": "multipart/form-data",
  //           },
  //         };
  //         const formData = new FormData();
  //         formData.append("productImage", productImage);
  //         formData.append("productImageName", productImage.name);
  //         formData.append("productId", product.id);

  //         await axios
  //           .post(config.path_api + "/productImage/insert", formData, _config)
  //           .then((res) => {
  //             if (res.data.message === "success") {
  //               Swal.fire({
  //                 title: "Upload Image",
  //                 text: "Upload Image Success",
  //                 icon: "success",
  //                 timer: 2000,
  //               })
  //                 .then(() => {
  //                   fetchDataProductImage({
  //                     id: product.id,
  //                   });
  //                   setProductImage("");
  //                 })
  //                 .catch((err) => {
  //                   throw err.response.data;
  //                 });
  //             }
  //           });
  //       } catch (e) {
  //         Swal.fire({
  //           title: "Error",
  //           text: e.message,
  //           icon: "error",
  //         });
  //       }
  //     }
  //   });
  // };

  const handleChangeFile = (files) => {
    setProductImage(files);
  };

  const handleUpload = () => {
    if (productImage.length > 0) {
      Swal.fire({
        title: "Upload Image",
        text: "Confirm Upload Image",
        icon: "question",
        showCancelButton: true,
        showConfirmButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          try {
            const _config = {
              headers: {
                Authorization:
                  "Bearer " + localStorage.getItem(config.token_name),
                "Content-Type": "multipart/form-data",
              },
            };

            // สร้างอาเรย์ของไฟล์ที่ถูกเลือก
            const selectedFiles = Array.from(productImage);
            // สร้างอัปโหลดแบบพร้อมอาเรย์ของไฟล์
            const uploadPromises = selectedFiles.map((productImage) => {
              const formData = new FormData();
              formData.append("productImage", productImage);
              formData.append("productImageName", productImage.name);
              formData.append("productId", product.id);

              // ส่งคำขออัปโหลดแต่ละไฟล์
              return axios.post(
                config.path_api + "/productImage/insert",
                formData,
                _config
              );
            });

            // รอสำเร็จของทุกคำขออัปโหลด
            await Promise.all(uploadPromises)
              .then((responses) => {
                const allUploadsSuccessful = responses.every(
                  (res) => res.data.message === "success"
                );

                if (allUploadsSuccessful) {
                  Swal.fire({
                    title: "Upload Image",
                    text: "Upload Image Success",
                    icon: "success",
                    timer: 2000,
                  }).then(() => {
                    fetchDataProductImage({
                      id: product.id,
                    });
                    setProductImage([]);
                  });
                } else {
                  Swal.fire({
                    title: "Error",
                    text: "Some file uploads failed",
                    icon: "error",
                  });
                }
              })
              .catch((err) => {
                throw err.response.data;
              });
          } catch (e) {
            Swal.fire({
              title: "Error",
              text: e.message,
              icon: "error",
            });
          }
        }
      });
    } else {
      Swal.fire({
        title: "Error",
        text: 'Please upload Images',
        icon: "error",
      });
    }
  };

  const handleChoose = (item) => {
    setProduct(item);
    fetchDataProductImage(item);
  };

  const handleChooseMainImage = (item) => {
    Swal.fire({
      title: "Choose Main Image",
      text: "Confirm Main Image",
      icon: "question",
      showCancelButton: true,
      showConfirmButton: true,
    }).then(async (res) => {
      if (res.isConfirmed) {
        try {
          const url =
            config.path_api +
            "/productImage/chooseMainImage/" +
            item.id +
            "/" +
            item.productId;
          await axios
            .get(url, config.headers())
            .then((res) => {
              if (res.data.message === "success") {
                fetchDataProductImage({
                  id: item.productId,
                });
                Swal.fire({
                  title: "Choose Main Image",
                  text: "Choose Main Image Successfully",
                  icon: "success",
                  timer: 2000,
                });
              }
            })
            .catch((err) => {
              throw err.response.data;
            });
        } catch (e) {
          Swal.fire({
            title: "Error",
            text: e.message,
            icon: "error",
          });
        }
      }
    });
  };

  const handleDeleteProductImage = (item) => {
    try {
      Swal.fire({
        title: "Delete Image",
        text: "Confirm Delete Image",
        icon: "question",
        showCancelButton: true,
        showConfirmButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          await axios
            .delete(
              config.path_api + "/productImage/delete/" + item.id,
              config.headers()
            )
            .then((res) => {
              if (res.data.message === "success") {
                fetchDataProductImage({
                  id: item.productId,
                });
                Swal.fire({
                  title: "Delete Image",
                  text: "Delete Image Successfully",
                  icon: "success",
                  timer: 2000,
                });
              }
            })
            .catch((err) => {
              throw err.response.data;
            });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const generateRandomBarcode = () => {
    const randomBarcode = Math.floor(Math.random() * 1000000);
    setProduct({ ...product, barcode: randomBarcode.toString() });
  };

  return (
    <>
      <Template>
        <div className="card">
          <div className=" card-header">
            <i className=" fa fa-box mr-2"></i>
            Product
          </div>
          <div className="card-body">
            <div
              onClick={handleClear}
              className="btn btn-primary btn-sm"
              data-bs-toggle="modal"
              data-bs-target="#ProductModal"
            >
              + Add Product
            </div>

            <table className="table table-bordered table-striped mt-3">
              <thead>
                <tr>
                  <th>Barcode</th>
                  <th>ProductName</th>
                  <th>Price</th>
                  <th>Cost</th>
                  <th>Detail</th>
                  <th style={{ width: 170 }}>Edit</th>
                </tr>
              </thead>
              <tbody>
                {products.length > 0 ? (
                  products.map((item) => (
                    <tr key={item.id}>
                      <td>{item.barcode}</td>
                      <td>{item.name}</td>
                      <td>{parseInt(item.price).toLocaleString("th-TH")}</td>
                      <td>{parseInt(item.cost).toLocaleString("th-Th")}</td>
                      <td>{item.detail}</td>
                      <td className="text-center">
                        <button
                          onClick={(e) => handleChoose(item)}
                          className="btn btn-primary mr-2"
                          data-bs-toggle="modal"
                          data-bs-target="#ProductImage"
                        >
                          <i className="fa fa-image"></i>
                        </button>
                        <button
                          onClick={(e) => setProduct(item)}
                          data-bs-toggle="modal"
                          data-bs-target="#ProductModal"
                          className="btn btn-warning mr-2"
                        >
                          <i className="fa fa-pencil"></i>
                        </button>
                        <button
                          onClick={(e) => handleDelete(item)}
                          className="btn btn-danger"
                        >
                          <i className="fas fa-eraser"></i>
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="6">No products available</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </Template>

      <Modal id="ProductModal" title="Add Product" modalSize="modal-lg">
        <form onSubmit={handleAddProduct} className=" was-validated row g-2">
          <div className="col-lg-3">
            <label htmlFor="productname" className=" form-label">
              Barcode
            </label>
            <input
              value={product.barcode}
              onChange={(e) =>
                setProduct({ ...product, barcode: e.target.value })
              }
              type="text"
              id="productname"
              className="form-control"
              required
            />
            <button
              type="button"
              onClick={generateRandomBarcode}
              className="btn btn-primary btn-sm mt-2 w-100"
            >
              Generate Barcode
            </button>
          </div>

          <div className="col-lg-9">
            <label htmlFor="barcode" className=" form-label">
              ProductName
            </label>
            <input
              value={product.name}
              onChange={(e) => setProduct({ ...product, name: e.target.value })}
              type="text"
              id="barcode"
              className="form-control"
              required
            />
            <div className="invalid-feedback">Product Name</div>
          </div>

          <div className="col-lg-3">
            <label htmlFor="price" className=" form-label">
              Price
            </label>
            <input
              value={product.price}
              onChange={(e) =>
                setProduct({ ...product, price: e.target.value })
              }
              type="number"
              id="price"
              className="form-control"
              required
            />
            <div className="invalid-feedback">Price</div>
          </div>
          <div className="col-lg-3">
            <label htmlFor="cost" className=" form-label">
              Cost
            </label>
            <input
              value={product.cost}
              onChange={(e) => setProduct({ ...product, cost: e.target.value })}
              type="number"
              id="cost"
              className="form-control"
              required
            />
            <div className="invalid-feedback">Cost</div>
          </div>
          <div className="col-lg-6">
            <label htmlFor="detail" className=" form-label">
              Detail
            </label>
            <input
              value={product.detail}
              onChange={(e) =>
                setProduct({ ...product, detail: e.target.value })
              }
              type="text"
              id="detail"
              className="form-control"
              required
            />
            <div className="invalid-feedback">Detail</div>
          </div>

          <div className="mt-3">
            <button onClick={handleAddProduct} className="btn btn-primary">
              Save
            </button>
          </div>
        </form>
      </Modal>

      <Modal id="ProductImage" title="Add Product Images" modalSize="modal-lg">
        <div className="row g-2">
          <div className="col-lg-4">
            <label htmlFor="" className="form-label">
              Barcode
            </label>
            <input
              value={product.barcode}
              type="text"
              disabled
              readOnly
              className="form-control"
            />
          </div>
          <div className="col-lg-8">
            <label htmlFor="" className="form-label">
              ProductName
            </label>
            <input
              value={product.name}
              type="text"
              disabled
              readOnly
              className="form-control"
            />
          </div>
          <div className="col-lg-12">
            <label htmlFor="" className="form-label">
              Detail
            </label>
            <input
              value={product.detail}
              type="text"
              disabled
              readOnly
              className="form-control"
            />
          </div>
          <div className="col-lg-12">
            <label htmlFor="" className="form-label">
              UploadFlieImage
            </label>
            <input
              onChange={(e) => handleChangeFile(e.target.files)}
              type="file"
              className="form-control"
              multiple
              accept=".png,.jpg"
              required
            />
          </div>

          <button onClick={(e) => handleUpload()} className="btn btn-primary">
            <i className="fa fa-check mr-2"></i>
            Upload Image
          </button>
        </div>
        <div className="row g-2 mt-3">
          <div className=" border-bottom fw-bold mb-3">
            <i className="fa fa-image mr-2"></i>Picture Product
          </div>
          {productImages.length > 0 ? (
            productImages.map((item) => (
              <div className="col-lg-3" key={item.id}>
                <div className="card">
                  <img
                    className="card-img-top"
                    src={config.path_api + "/upload/" + item.imageName}
                    alt=""
                  />
                  <div className="p-1">
                    {item.isMain ? (
                      <div>
                        <button className="btn btn-primary btn-sm w-75">
                          Main
                        </button>
                        <button
                          onClick={(e) => handleDeleteProductImage(item)}
                          className="btn btn-danger btn-sm w-25"
                        >
                          X
                        </button>
                      </div>
                    ) : (
                      <div>
                        <button
                          onClick={(e) => handleChooseMainImage(item)}
                          className="btn btn-default btn-sm w-75"
                        >
                          Not Main
                        </button>
                        <button
                          onClick={(e) => handleDeleteProductImage(item)}
                          className="btn btn-danger btn-sm w-25"
                        >
                          X
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="text-start">Not found Picture Product</div>
          )}
        </div>
      </Modal>
    </>
  );
}

export default Product;
