import axios from "axios";
import { useState } from "react";
import Swal from "sweetalert2";
import config from "../config";
import { useNavigate } from "react-router-dom";
function Login() {
  const [phone, setPhone] = useState("");
  const [pass, setPass] = useState("");

  const navgate = useNavigate();

  const handleLogin = async () => {
    try {
      const payload = {
        phone: phone,
        pass: pass,
      };

      await axios
        .post(config.path_api + "/member/singin", payload)
        .then((res) => {
          if (res.data.message === 'success') {
              Swal.fire({
                  icon: 'success',
                  title: 'Sing In',
                  text: 'เข้าสู่ระบบสำเร็จ',
                  timer: 2000
              }).then(()=>{
                localStorage.setItem(config.token_name, res.data.token);
                navgate('/home');
              })
          }
        });
    } catch (e) {
      Swal.fire({
        icon: "error",
        title: "error",
        text: e.message,
      });
    }
  };
  return (
    <>
      <div className="container py-5">
        <div className="card shadow px-2">
        <h1 className="text-center text-bg-primary rounded p-3 shadow mt-3">
          Pages Login to POS
        </h1>
          <div className="card-body">
            <div className="row g-2 was-validated">
              <div className="col-md-12">
                <label htmlFor="" className="form-label">
                  เบอร์โทร
                </label>
                <input
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  type="text"
                  className="form-control"
                  required
                />
                <div className=" invalid-feedback">
                  กรุณากรอกเบอร์ที่ลงทะเบียน
                </div>
              </div>
              <div className="col-md-12">
                <label htmlFor="" className="form-label">
                  รหัสผ่าน
                </label>
                <input
                  value={pass}
                  onChange={(e) => setPass(e.target.value)}
                  type="password"
                  className="form-control"
                  required
                />
                <div className=" invalid-feedback">กรุณากรอกรหัสผ่าน</div>
              </div>
              <button className="btn btn-primary" onClick={handleLogin}>
                <i className="fa fa-check-circle"></i> ล็อคอินเข้าสู่ระบบ
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
