import axios from "axios";
import { useEffect, useState, forwardRef, useImperativeHandle } from "react";
import config from "../config";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import Modal from "./Modal";

const Sidebar = forwardRef((props, sidebarRef) => {
  const [packageName, setPackageName] = useState("");
  const [memberName, setMemberName] = useState("");
  const [packages, setPackages] = useState([]);
  const [totalBill, setTotalBill] = useState(0);
  const [billAmount, setBillAuomnt] = useState(0);
  const [bank, setBank] = useState({});
  const [choosePackage, setChoosePackage] = useState({});

  useEffect(() => {
    fetchData();
    fetchDataTotalBill();
  }, []);

  const fetchData = async () => {
    try {
      axios
        .get(config.path_api + "/member/info", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setMemberName(res.data.result.name);
            setPackageName(res.data.result.package.name);
            setBillAuomnt(res.data.result.package.bill_amount);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchDataPackage = async () => {
    try {
      await axios
        .get(config.path_api + "/package/list", config.headers())
        .then((res) => {
          if (res.data.results.length > 0) {
            setPackages(res.data.results);
            
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchDataTotalBill = async () => {
    try {
      await axios
        .get(config.path_api + "/package/countBill", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setTotalBill(res.data.results);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchDateBank = async () => {
    try {
      await axios
        .get(config.path_api + "/bank/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setBank(res.data.results);
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleChoosePackage = () => {
    try {
      Swal.fire({
        title: "Confirm Register Package!",
        text: "กรุณาตรวจสอบข้อมูลแพ็คเกจถูกต้องหรือไหม",
        icon: "question",
        showConfirmButton: true,
        showCancelButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          await axios
            .get(
              config.path_api + "/package/changePackage/" + choosePackage.id,
              config.headers()
            )
            .then((res) => {
              if (res.data.message === "success") {
                Swal.fire({
                  title: "บันทึกข้อมูลสำเร็จ",
                  text: "บันทึกข้อมูลการสมัครแพ็คเจใหม่เรียบร้อย",
                  icon: "success",
                });

                const btns = document.getElementsByClassName("btnClose");
                for (let i = 0; i < btns.length; i++) btns[i].click();
              }
            })
            .catch((err) => {
              // throw err.response.data;
              if (err.response.data.status === 409) {
                Swal.fire({
                  title: "ท่านได้ส่งข้อมูลสมัครแพ็คเกจนี้แล้ว",
                  text: "กรุณาเลือกแพ็คเกจอื่นที่ไม่ใช้แพ็คเกจเดิม",
                  icon: "warning",
                });
              }
            });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleChooseBank = (item) => {
    setChoosePackage(item);
    fetchDateBank();
  };

  useImperativeHandle(sidebarRef, () => ({
    refreshCountBill() {
      fetchDataTotalBill();
    },
  }));

  const renderButton = (item) => {
    if (packageName === item.name) {
      return (
        <button className="btn btn-primary mt-3 w-100 disabled">
          <i className="fa fa-check me-2"></i>
          Select Package
        </button>
      );
    } else {
      return (
        <button
          onClick={(e) => handleChooseBank(item)}
          data-bs-toggle="modal"
          data-bs-target="#modalBank"
          className="btn btn-primary mt-3 w-100"
        >
          <i className="fa fa-check me-2"></i>
          Select Package
        </button>
      );
    }
  };

  const computePercen = (totalBill, billAmount) => {
    if (billAmount > 0) {
      return (totalBill * 100) / billAmount;
    } else {
      return 0;
    }
  };

  return (
    <>
      <aside className="main-sidebar sidebar-dark-primary elevation-4">
        <a href="index3.html" className="brand-link">
          <img
            src="dist/img/AdminLTELogo.png"
            alt="AdminLTE Logo"
            className="brand-image img-circle elevation-3"
            style={{ opacity: 0.8 }}
          />
          <span className="brand-text font-weight-light">POS on Cloud</span>
        </a>

        <div className="sidebar">
          <div className="user-panel mt-3 pb-3 mb-3 d-flex">
            <div className="image">
              <img
                src="dist/img/user2-160x160.jpg"
                className="img-circle elevation-2"
                alt="Profile picture of user John Doe"
              />
            </div>
            <div className="info text-white">
              <div>Shop : {memberName}</div>
              <div>Package : {packageName}</div>
              <button
                onClick={fetchDataPackage}
                data-bs-toggle="modal"
                data-bs-target="#modalPackage"
                className="btn btn-warning btn-sm mt-2 w-100"
              >
                Upgrade
              </button>
            </div>
          </div>
          <div className=" text-white mx-2">
            <div className="justify-content-between d-flex">
              <div className="float-start">
                {totalBill}/{billAmount.toLocaleString("th-TH")}
              </div>
              <div className="float-end">
                {billAmount > 0 ? computePercen(totalBill, billAmount) : "0"} %
              </div>
            </div>
          </div>
          <div className="progress mx-2">
            <div
              className="progress-bar"
              role="progressbar"
              aria-valuenow={totalBill}
              aria-valuemin="0"
              aria-valuemax="100"
              style={{ width: computePercen(totalBill, billAmount) + "%" }}
            ></div>
          </div>

          <nav className="mt-2">
            <ul
              className="nav nav-pills nav-sidebar flex-column"
              data-widget="treeview"
              role="menu"
              data-accordion="false"
            >
              <li className="nav-item">
                <Link to="/home" className="nav-link">
                  <i className="nav-icon fa fa-dashboard"></i>
                  <p>Dashboard</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/product" className="nav-link">
                  <i className="nav-icon fa fa-box"></i>
                  <p>Product</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/sale" className="nav-link">
                  <i className="nav-icon fa fa-dollar-sign"></i>
                  <p>Sale product</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/user" className="nav-link">
                  <i className="nav-icon fa fa-person"></i>
                  <p>User System</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/sumsalePerDay" className="nav-link">
                  <i className="nav-icon fa fa-file"></i>
                  <p>Daily Sales Report</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/billsale" className="nav-link">
                  <i className="nav-icon fa fa-file-alt"></i>
                  <p>Sales Bill Report</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/stock" className="nav-link">
                  <i className="nav-icon fa fa-home"></i>
                  <p>Products Into Stock</p>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/reportstock" className="nav-link">
                  <i className="nav-icon fa fa-repeat"></i>
                  <p>Report Stock</p>
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      </aside>

      <Modal id="modalPackage" title="Select Package" modalSize="modal-lg">
        <div className="row g-2">
          {packages.length > 0
            ? packages.map((item) => (
                <div key={item.id} className="col-lg-4">
                  <div className="card">
                    <div className="card-body">
                      <div className="h1 text-center text-primary fw-bold">
                        {item.name}
                      </div>
                      <div className="mt-3 text-center">
                        Price{": "}
                        <span className="text-danger fw-bold">
                          {parseInt(item.price).toLocaleString("th-TH")}
                        </span>{" "}
                        .- /Month
                      </div>
                      <div className="mt-3 text-center">
                        amount{": "}
                        <span className="text-info">
                          {parseInt(item.bill_amount).toLocaleString("th-TH")}
                        </span>{" "}
                        Bill/Month{" "}
                      </div>
                      {renderButton(item)}
                    </div>
                  </div>
                </div>
              ))
            : null}
        </div>
      </Modal>
      <Modal id="modalBank" title="บัญชีธนาคาร" modalSize="modal-lg">
        <div className="h3">
          Package ที่เลือกคือ{" "}
          <span className="text-primary fw-bold">{choosePackage.name}</span>
        </div>

        <div className="mt-3 h5">
          Price{" "}
          <span className="text-danger">
            {parseInt(choosePackage.price).toLocaleString("th-TH")}
          </span>{" "}
          .-/ Month
        </div>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th>ชื่อธนาคาร</th>
              <th>เลขบัญชี</th>
              <th>เจ้าของบัญชี</th>
              <th>สาขา</th>
            </tr>
          </thead>
          <tbody>
            {bank.length > 0
              ? bank.map((item) => (
                  <tr key={item.id}>
                    <td>{item.bankName}</td>
                    <td>{item.bankCode}</td>
                    <td>{item.bankType}</td>
                    <td>{item.bankBranch}</td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
        <div className=" alert alert-default-warning">
          <i className="fa fa-info-circle me-2"></i>
          เมื่อโอนชำระเงินค่า Package เรียบให้แจ้งได้ที่ ID = PHOP
        </div>
        <div className="row g-2r">
          <div className="col-lg-6 mx-auto">
            <button
              onClick={handleChoosePackage}
              className="btn btn-primary w-100"
            >
              <i className="fa fa-save me-2"></i>
              ส่งข้อมูลการสั่งซื้อแพ็คเกจ
            </button>
          </div>
        </div>
      </Modal>
    </>
  );
});
export default Sidebar;
