import Template from "../components/Template";
import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import * as dayJs from "dayjs";
function Billsale() {
  const [billsale, setBillsale] = useState([]);
  const [selectBill, setSelectBill] = useState({});
  const [totalBill, setTotalBill] = useState([]);

  useEffect(() => {
    fetchDate();
  }, []);

  const fetchDate = async () => {
    try {
      await axios
        .get(config.path_api + "/billsale/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setBillsale(res.data.results);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const sumTotalBill = (item) => {
    let sum = 0;
    for (let i = 0; i < item.billsaledetails.length; i++) {
        const items = item.billsaledetails[i];
        const qty = parseInt(items.qty);
        const price = parseInt(items.price);

        sum += qty * price;
    }
    setTotalBill(sum)
  };

  return (
    <>
      <Template>
        <div className="card">
          <div className="card-header">
            <div className="card-title">
              <div className="">Sale bill report</div>
            </div>
            <div className="card-body">
              <table className="table table-bordered table-striped">
                <thead>
                  <tr>
                    <th style={{ width: 150 }}>Previwe</th>
                    <th>Id</th>
                    <th>CreateAt</th>
                  </tr>
                </thead>
                <tbody>
                  {billsale.length > 0
                    ? billsale.map((item) => (
                        <tr key={item.id}>
                          <td className="text-center">
                            <button
                              onClick={(e) => {
                                setSelectBill(item);
                                sumTotalBill(item);
                              }}
                              data-bs-toggle="modal"
                              data-bs-target="#billsaleDetailModal"
                              className="btn btn-primary"
                            >
                              <i className="fa fa-eye me-2"></i>
                              Preview
                            </button>
                          </td>
                          <td>{item.id}</td>
                          <td>
                            {dayJs(item.createdAt).format(
                              "YYYY-MM-DD / HH:mm:ss"
                            )}
                          </td>
                        </tr>
                      ))
                    : null}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </Template>

      <Modal
        id="billsaleDetailModal"
        title="Preview bill sale report"
        modalSize="modal-lg"
      >
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>List</th>
              <th className="text-end">Pirce</th>
              <th className="text-end">Amount</th>
              <th className="text-end">Total</th>
            </tr>
          </thead>
          <tbody>
            {selectBill.billsaledetails !== undefined &&
            selectBill.billsaledetails.length > 0
              ? selectBill.billsaledetails.map((item) => (
                  <tr key={item.id}>
                    <td>{item.product.name}</td>
                    <td className="text-end">{parseInt(item.price).toLocaleString("th-TH")}</td>
                    <td className="text-end">{item.qty}</td>
                    <td className="text-end">
                      {parseInt(item.qty * item.price).toLocaleString("th-TH")}
                    </td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
        <p className="text-end">Total price: {totalBill.toLocaleString("th-TH") + " Bath"}</p>
      </Modal>
    </>
  );
}

export default Billsale;
