import { useEffect, useState } from "react";
import Template from "../components/Template";
import Swal from "sweetalert2";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import * as dayJs from "dayjs";

function SumsalePerDay() {
  const [currentYear, setCurrentYear] = useState(() => {
    let myDate = new Date();
    return myDate.getFullYear();
  });

  const [arrYear, setArrYear] = useState(() => {
    let arr = [];
    let myDate = new Date();
    let currentYear = myDate.getFullYear();
    let beforeYear = currentYear - 5;

    for (let i = beforeYear; i <= currentYear; i++) {
      arr.push(i);
    }
    return arr;
  });

  const [currentMonth, setCurrentMonth] = useState(() => {
    let myDate = new Date();
    return myDate.getMonth() + 1;
  });

  const [arrMonth, setArrMonth] = useState(() => {
    let arr = [
      { value: 1, label: "January" },
      { value: 2, label: "February" },
      { value: 3, label: "March" },
      { value: 4, label: "April" },
      { value: 5, label: "May" },
      { value: 6, label: "June" },
      { value: 7, label: "July" },
      { value: 8, label: "August" },
      { value: 9, label: "September" },
      { value: 10, label: "October" },
      { value: 11, label: "November" },
      { value: 12, label: "December" },
    ];

    return arr;
  });

  const [billsale, setBillsale] = useState([]);
  const [currentBillSale, setCurrentBillSale] = useState({});
  const [billSaleDetail, setBillSaleDetaill] = useState([]);
  const [totalBill, setTotalBill] = useState([]);


  useEffect(() => {
    handleShowReport();
  }, []);

  const handleShowReport = async () => {
    try {
      const urlPath =
        config.path_api +
        "/billSale/listbyYearAndMonth/" +
        currentYear +
        "/" +
        currentMonth;
      await axios
        .get(urlPath, config.headers())
        .then((res) => {
          setBillsale(res.data.results);
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };
  const sumTotalBill = (item) => {
    const billsaleDetail = item.billsaledetails
    
    let sum = 0;
    for (let i = 0; i < billsaleDetail.length; i++) {
        const items = billsaleDetail[i];
        const qty = parseInt(items.qty);
        const price = parseInt(items.price);
        sum += qty * price;
      }

      setTotalBill(sum)

  };

  return (
    <>
      <Template>
        <div className="card">
          <div className="card-header">
            <div className="card-title">Daily Sales Report</div>
          </div>
          <div className="card-body">
            <div className="row g-2">
              <div className="col-lg-3">
                <div className="input-group">
                  <label htmlFor="" className="input-group-text">
                    Year
                  </label>
                  <select
                    onChange={(e) => setCurrentYear(e.target.value)}
                    value={currentYear}
                    className="form-control"
                  >
                    {arrYear.map((item) => (
                      <option key={item} value={item}>
                        {item}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              <div className="col-lg-3">
                <div className="input-group">
                  <label htmlFor="" className="input-group-text">
                    Month
                  </label>
                  <select
                    onChange={(e) => setCurrentMonth(e.target.value)}
                    value={currentMonth}
                    className="form-control"
                  >
                    {arrMonth.map((item) => (
                      <option key={item.value} value={item.value}>
                        {item.label}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              <div className="col-lg-3">
                <button onClick={handleShowReport} className="btn btn-primary">
                  <i className="fa fa-check me-2"></i>
                  Show
                </button>
              </div>
            </div>
          </div>
          <div className="card-body">
            <table className="table table-bordered table-striped">
              <thead>
                <tr>
                  <th className="text-center" style={{ width: 180 }}>
                    Preview
                  </th>
                  <th className="text-center" style={{ width: 100 }}>
                    Day
                  </th>
                  <th className="text-end">Total</th>
                </tr>
              </thead>
              <tbody>
                {billsale.length > 0
                  ? billsale.map((item) => (
                      <tr key={item.day}>
                        <td className="text-center">
                          <button
                            data-toggle="modal"
                            data-target="#modalCurrentBillSale"
                            onClick={(e) => setCurrentBillSale(item.results)}
                            className="btn btn-primary"
                          >
                            <i className="fa fa-eye me-2"></i>
                            Preview
                          </button>
                        </td>
                        <td className="text-center">{item.day}</td>
                        <td className="text-end">
                          {parseInt(item.sum).toLocaleString("th-TH")} .-
                        </td>
                      </tr>
                    ))
                  : null}
              </tbody>
            </table>
          </div>
        </div>
      </Template>

      <Modal id="modalCurrentBillSale" title="Bill Sale">
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>Preview</th>
              <th className="text-end">Bill Number</th>
              <th className="text-end">Date</th>
            </tr>
          </thead>
          <tbody>
            {currentBillSale.length > 0
              ? currentBillSale.map((item) => (
                  <tr key={item.id}>
                    <td>
                      <button 
                      onClick={e =>{setBillSaleDetaill(item.billsaledetails); sumTotalBill(item)}}
                      data-toggle="modal"
                      data-target="#modalCurrentBillSaleDetail"
                      className="btn btn-primary">
                        <i className="fa fa-eye me-2"></i>
                        Preview
                      </button>
                    </td>
                    <td className="text-end">{item.id}</td>
                    <td className="text-end">
                      {dayJs(item.createdAt).format("YYYY-MM-DD / HH:mm:ss")}
                    </td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
      </Modal>

      <Modal id="modalCurrentBillSaleDetail" title="Sales bill details" modalSize="modal-lg">
          <table className="table table-bordered table-striped">
                <thead>
                  <tr>
                    <th>List</th>
                    <th className="text-end">Price</th>
                    <th className="text-end">Amount</th>
                    <th className="text-end">Total</th>
                  </tr>
                </thead>
                <tbody>
                  {billSaleDetail.length > 0 ? billSaleDetail.map(item => 
                    <tr key={item.id}>
                      <td>{item.product.name}</td>
                      <td className="text-end">{item.price}</td>
                      <td className="text-end">{item.qty}</td>
                      <td className="text-end">
                        {parseInt(item.qty * item.price).toLocaleString("th-TH")}
                      </td>
                    </tr>
                  ): null}
                </tbody>
          </table>
          <p className="text-end">Total price: {totalBill.toLocaleString("th-TH") + " Bath"}</p>
      </Modal>
    </>
  );
}

export default SumsalePerDay;
