import { useEffect, useState } from "react";
import Template from "../components/Template";
import Swal from "sweetalert2";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import * as dayJs from "dayjs";

function Stock() {
  const [product, setProduct] = useState([]);
  const [productName, setProductName] = useState("");
  const [productId, setProductId] = useState(0);
  const [qty, setQty] = useState(0);
  const [stock, setStock] = useState([]);

  useEffect(() => {
    fetchDataStock();
  }, []);

  const fetchData = async () => {
    try {
      await axios
        .get(config.path_api + "/product/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setProduct(res.data.result);
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchDataStock = async () => {
    try {
      await axios
        .get(config.path_api + "/stock/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setStock(res.data.results);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        tite: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleChooseProduct = (item) => {
    setProductName(item.name);
    setProductId(item.id);

    const btns = document.getElementsByClassName("btnClose");
    for (let i = 0; i < btns.length; i++) btns[i].click();
  };

  const handleSave = async () => {
    try {
      // Check if qty is 0 or productId is empty
      if (qty === 0 || productId === "" || productName === "") {
        Swal.fire({
          title: "Error",
          text: "Error กรอกข้อมูลไม่ครบ",
          icon: "error",
        });
        return;
      }
      const payload = {
        qty: qty,
        productId: productId,
      };

      await axios
        .post(config.path_api + "/stock/save", payload, config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            fetchDataStock();
            Swal.fire({
              title: "Save successfully",
              text: "Save amount product into stock",
              icon: "success",
              timer: 1000,
            });
            setProductName("");
            setQty(0);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleDelete = async (item) =>{
    try {
      Swal.fire({
        title: 'Confirm Your delete',
        text: 'Your Confirm Delete Stock',
        icon: 'question',
        showCancelButton: true,
        showConfirmButton: true,
      }).then(async res =>{
        if (res.isConfirmed) {
            await axios.delete(config.path_api + '/stock/delete/' + item.id , config.headers()).then(res =>{
                if (res.data.message  === 'success') {
                  fetchDataStock();
                  Swal.fire({
                    title: 'Successfully',
                    text: 'Delect Stock Success',
                    icon: 'success',
                    timer: 1000
                  })
                }
            }).catch(err =>{
              throw err.response.data;
            })
        }
      })
      
    } catch (e) {
      Swal.fire({
        title: 'Error',
        text: e.message,
        icon: 'error'
      })
    }
  }
  return (
    <>
      <Template>
        <div className="card">
          <div className="card-header">
            <div className="card-title">Product into Stock</div>
          </div>
          <div className="card-body">
            <div className="row g-2">
              <div className="col-lg-4">
                <div className="input-group">
                  <label className="input-group-text">Product</label>
                  <input
                    value={productName}
                    type="text"
                    className="form-control"
                    disabled
                  />
                  <button
                    onClick={fetchData}
                    data-bs-toggle="modal"
                    data-bs-target="#modalProduct"
                    className="btn btn-primary"
                  >
                    <i className="fa fa-search"></i>
                  </button>
                </div>
              </div>
              <div className="col-lg-3">
                <div className="input-group">
                  <label className="input-group-text">Amount</label>
                  <input
                    value={qty}
                    onChange={(e) => setQty(e.target.value)}
                    type="number"
                    className="form-control"
                  />
                </div>
              </div>
              <div className="col-lg-4">
                <button onClick={handleSave} className="btn btn-primary">
                  <i className="fa fa-save me-2"></i>
                  Save
                </button>
              </div>
            </div>
          </div>
          <div className="card-body">
            <table className="table table-bordered table-striped">
              <thead>
                <tr>
                  <th>Barcode</th>
                  <th>Product Name</th>
                  <th className="text-end">Amount Stock</th>
                  <th className="text-end">Update</th>
                  <th className="text-center">Delect</th>
                </tr>
              </thead>
              <tbody>
                {stock.length > 0
                  ? stock.map((item) => (
                      <tr key={item.id}>
                        <td>{item.product.barcode}</td>
                        <td>{item.product.name}</td>
                        <td className="text-end">{item.qty}</td>
                        <td className="text-end">
                          {dayJs(item.createdAt).format("YYYY/MM/DD HH:mm")}
                        </td>
                        <td className="text-center">
                          <button
                            onClick={(e) => handleDelete(item)}
                            className="btn btn-danger btn-sm w-75"
                          >
                            <i className="fa fa-times me-2"></i>
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))
                  : null}
              </tbody>
            </table>
          </div>
        </div>
      </Template>

      <Modal id="modalProduct" title="Product select" modalSize="modal-lg">
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th className="text-center" style={{ width: 150 }}>
                Select
              </th>
              <th>Barcode</th>
              <th>Product name</th>
            </tr>
          </thead>
          <tbody>
            {product.length > 0
              ? product.map((item) => (
                  <tr key={item.id}>
                    <td className="text-center">
                      <button
                        onClick={(e) => handleChooseProduct(item)}
                        className="btn btn-primary"
                      >
                        <i className="fa fa-check me-2"></i>
                        Select
                      </button>
                    </td>
                    <td>{item.barcode}</td>
                    <td>{item.name}</td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
      </Modal>
    </>
  );
}
export default Stock;
