import { useState, useEffect } from "react";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import Swal from "sweetalert2";
import Template from "../components/Template";
// import { useNavigate } from "react-router-dom";
function User() {
  const [user, setUser] = useState({
    name: "",
    usr: "",
    level: "user",
  });
  const [users, setUsers] = useState([]);
  const [password, setPassword] = useState("");
  const [passwordconfirm, setPasswordConfirm] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordConfirm, setShowPasswordConfirm] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      await axios
        .get(config.path_api + "/user/list", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setUsers(res.data.result);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleSaveUser = async (e) => {
    e.preventDefault();
    try {
      let url = config.path_api + "/user/insert";

      if (user.id !== undefined) {
        url = config.path_api + "/user/edit";
      }

      await axios
        .post(url, user, config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            Swal.fire({
              title: "Create User System",
              text: "Create User System Successfully",
              icon: "success",
              timer: 2000,
            }).then(() => {
              fetchData();
              handleClose();
            });
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const changePassword = (item) => {
    setPassword(item);
    comparePassword();
  };

  const changePasswordConfirm = (item) => {
    setPasswordConfirm(item);
    comparePassword();
  };

  const comparePassword = () => {
    if (password.length > 0 && passwordconfirm.length > 0) {
      if (password !== passwordconfirm) {
        Swal.fire({
          title: "ตรวจสอบการกรอกรหัสผ่าน",
          text: "โปรดกรอกรหัสผ่านให้ตรงกัน",
          icon: "error",
        });
      } else {
        setUser({
          ...user,
          pws: password,
        });
      }
    }
  };

  const handleDelete = (item) => {
    try {
      const itemId = item.id;
      Swal.fire({
        title: "Confirm Delete",
        text: "Your Confirm Delete",
        icon: "question",
        showCancelButton: true,
        showConfirmButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          await axios
            .delete(
              config.path_api + "/user/delete/" + itemId,
              config.headers()
            )
            .then((res) => {
              if (res.data.message === "success") {
                Swal.fire({
                  title: "Delete User System",
                  text: "Succesfully Delete user system",
                  icon: "success",
                  timer: 2000,
                });
                fetchData();
              }
            });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleClose = () => {
    const btns = document.getElementsByClassName("btnClose");
    for (let i = 0; i < btns.length; i++) {
      btns[i].click();
    }
  };
  const clearForm = () => {
    setUser({ name: "", usr: "", level: "user" });
    setPassword("");
    setPasswordConfirm("");
  };

  const handleSeePassword = () => {
    setShowPassword(!showPassword);
  };

  const handleSeePasswordConfirm = () => {
    setShowPasswordConfirm(!showPasswordConfirm);
  };

  return (
    <>
      <Template>
        <div className="card">
          <div className=" card-header">
            <i className=" fa fa-person mr-2"></i>
            User System
          </div>
          <div className="card-body">
            <div
              onClick={clearForm}
              className="btn btn-primary btn-sm"
              data-bs-toggle="modal"
              data-bs-target="#UserModal"
            >
              + Add User
            </div>
            <table className="table table-bordered table-striped mt-3">
              <thead>
                <tr>
                  <th>No.</th>
                  <th>Name</th>
                  <th>Username</th>
                  <th>Level</th>
                  <th style={{ width: 150 }}>Edit</th>
                </tr>
              </thead>
              <tbody>
                {users && users.length > 0 ? (
                  users.map((item, index) => (
                    <tr key={item.id}>
                      <td>{index + 1}</td>
                      <td>{item.name}</td>
                      <td>{item.usr}</td>
                      <td>{item.level}</td>
                      <td className="text-center">
                        <button
                          onClick={(e) => {
                            setUser(item);
                            setPassword(item);
                            setPasswordConfirm(item);
                          }}
                          data-bs-toggle="modal"
                          data-bs-target="#UserModal"
                          className="btn btn-warning mr-2"
                        >
                          <i className="fa fa-pencil"></i>
                        </button>
                        <button
                          onClick={(e) => handleDelete(item)}
                          className="btn btn-danger"
                        >
                          <i className="fa fa-times"></i>
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="4" className="text-center">
                      Not found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </Template>

      <Modal
        id="UserModal"
        icon={<i className="fa fa-person mr-2"></i>}
        title="Create User System"
        modalSize="modal-md"
      >
        <form onSubmit={handleSaveUser} className="row g-2 was-validated">
          <div className="col-lg-12 mt-3">
            <label htmlFor="txtName" className="form-label">
              Name
            </label>
            <input
              value={user.name}
              onChange={(e) => setUser({ ...user, name: e.target.value })}
              type="text"
              className="form-control"
              required
            />
          </div>
          <div className="col-lg-12 mt-3">
            <label htmlFor="txtUser" className="form-label">
              Username
            </label>
            <input
              value={user.usr}
              onChange={(e) => setUser({ ...user, usr: e.target.value })}
              type="text"
              className="form-control"
              required
            />
          </div>
          <div className="col-lg-12 mt-3">
            <label htmlFor="txtPassword" className="form-label">
              Password
            </label>
            <div className="input-group">
              <input
                value={password}
                type={showPassword ? "text" : "password"}
                onChange={(e) => setPassword(e.target.value)}
                onBlur={(e) => changePassword(e.target.value)}
                className="form-control"
                required
              />
              <button
                type="button"
                onClick={handleSeePassword}
                className="btn btn-default"
              >
                {showPassword ? "Hide" : "Show"}
              </button>
            </div>
          </div>
          <div className="col-lg-12 mt-3">
            <label htmlFor="txtConfirmPassword" className="form-label">
              Confirm Password
            </label>
            <div className="input-group">
              <input
                value={passwordconfirm}
                type={showPasswordConfirm ? "text" : "password"}
                onChange={(e) => setPasswordConfirm(e.target.value)}
                onBlur={(e) => changePasswordConfirm(e.target.value)}
                className="form-control"
                required
              />
              <button type="button" onClick={handleSeePasswordConfirm} className="btn btn-default">
                {showPasswordConfirm ? "Hide" : "Show"}
              </button>
            </div>
          </div>
          <div className="col-lg-12 mt-3">
            <label htmlFor="txtLevel" className="form-label">
              Level
            </label>
            <select
              value={user.level}
              onChange={(e) => setUser({ ...user, level: e.target.value })}
              className="form-select"
              required
            >
              <option value={""} disabled>
                Select
              </option>
              <option value={"user"}>User</option>
              <option value={"admin"}>Admin</option>
            </select>
          </div>
          <div className="col-lg-12 mt-3">
            <button
              onClick={(e) => handleSaveUser}
              type="submit"
              className="btn btn-primary w-100"
            >
              Confirm
            </button>
          </div>
        </form>
      </Modal>
    </>
  );
}

export default User;
