import { useEffect, useState } from "react";
import Template from "../components/Template";
import Swal from "sweetalert2";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import * as dayjs from "dayjs"

function ReportStock() {
  const [stocks, setStocks] = useState([]);
  const [currentstocks, setCurrentStocks] = useState([]);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      await axios
        .get(config.path_api + "/stock/report", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setStocks(res.data.results);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };
  return (
    <>
      <Template>
        <div className="card">
          <div className="card-header">
            <div className="card-title">Report Stock</div>
          </div>
          <div className="card-body">
            <table className="table table-bordered table-striped">
              <thead>
                <tr>
                  <th>Barcode</th>
                  <th>List</th>
                  <th className="text-end">Input</th>
                  <th className="text-end">Output</th>
                  <th className="text-end">Product remaining</th>
                </tr>
              </thead>
              <tbody>
                {stocks.length > 0
                  ? stocks.map((item) => (
                      <tr key={item.result.id}>
                        <td>{item.result.barcode}</td>
                        <td>{item.result.name}</td>
                        <td className="text-end">
                          <a
                            onClick={(e) => setCurrentStocks(item)}
                            data-bs-toggle="modal"
                            data-bs-target="#modalStockIn"
                            className="btn btn-link text-success"
                          >
                            {item.stockIn}
                          </a>
                        </td>
                        <td className="text-end">
                          <a
                            onClick={(e) => setCurrentStocks(item)}
                            data-bs-toggle="modal"
                            data-bs-target="#modalStockOut"
                            className="btn btn-link text-danger"
                          >
                            {item.stockOut}
                          </a>
                        </td>
                        <td className="text-end">
                          {item.stockIn - item.stockOut}
                        </td>
                      </tr>
                    ))
                  : null}
              </tbody>
            </table>
          </div>
        </div>
      </Template>

      <Modal id="modalStockIn" title="Data Input" modalSize="modal-lg">
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>Barcode</th>
              <th>Product name</th>
              <th className="text-end">Amount</th>
              <th className="text-end">Date</th>
            </tr>
          </thead>
          <tbody>
            {currentstocks.result !== undefined
              ? currentstocks.result.stocks.map((item) => (
                  <tr key={item.id}>
                    <td>{item.product.barcode}</td>
                    <td>{item.product.name}</td>
                    <td className="text-end">{item.qty}</td>
                    <td className="text-end">{dayjs(item.createdAt).format("DD/MM/YY HH:mm")}</td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
      </Modal>

      <Modal id="modalStockOut" title="Data Output" modalSize="modal-lg">
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>Barcode</th>
              <th>Product name</th>
              <th className="text-end">Amount</th>
              <th className="text-end">Date</th>
            </tr>
          </thead>
          <tbody>
            {currentstocks.result !== undefined
              ? currentstocks.result.billsaledetails.map((item) => (
                  <tr key={item.id}>
                    <td>{item.product.barcode}</td>
                    <td>{item.product.name}</td>
                    <td className="text-end">{item.qty}</td>
                    <td className="text-end">{dayjs(item.createdAt).format("DD/MM/YY HH:mm")}</td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
      </Modal>
    </>
  );
}

export default ReportStock;
