import { useEffect, useRef, useState } from "react";
import Template from "../components/Template";
import Swal from "sweetalert2";
import axios from "axios";
import config from "../config";
import Modal from "../components/Modal";
import dayjs, * as dayJs from "dayjs";
import PrintJS from "print-js";

function Sale() {
  const [product, setProduct] = useState([]);
  const [openBill, setOpenBill] = useState({});
  const [currentBill, setCurrentBill] = useState([]);
  const [totalPirce, setTotalPrice] = useState([]);
  const [totalPirceLastBill, setTotalPriceLastBill] = useState([]);
  const [totalPircePreviewBillSale, setTotalPricePreviewBillSale] = useState(
    []
  );
  const [item, setItem] = useState({
    qty: "",
  });
  const [inputMoney, setInputMoney] = useState(0);
  const [lastbill, setLastBill] = useState({});
  const [billToday, setBillToday] = useState([]);
  const [preViewBillSale, setPreviewBillSale] = useState({});
  const [memberinfo, setMemberInfo] = useState({});


  const saleRef = useRef();

  useEffect(() => {
    fetchData();
    openBillSale();
    fetchDateSale();
    let slip = document.getElementById("slip");
    slip.style.display = "none";
    // กดสั่งปริ้น ป้องการเรียกข้อมูลไม่ทัน ให้ มาทำการตรวจสอบ memberinfo ก่อน หากมีข้อมูลค่อย ทำงาน
    if (
      memberinfo.name !== undefined &&
      lastbill != {} &&
      lastbill.billsaledetails !== undefined
    ) {
      slip.style.display = "block";
      PrintJS({
        printable: "slip",
        maxWidth: 250,
        type: "html",
      });
      slip.style.display = "none";
    }
  }, [memberinfo, lastbill]);



  const openBillSale = async () => {
    try {
      await axios
        .get(config.path_api + "/billsale/openbillsale", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setOpenBill(res.data.result);
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchData = async () => {
    try {
      await axios
        .get(config.path_api + "/product/listForSale", config.headers())
        .then((res) => {
          setProduct(res.data.result);
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleSave = async (item) => {
    try {
      await axios
        .post(config.path_api + "/billsale/sale", item, config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            fetchDateSale();
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const fetchDateSale = async () => {
    try {
      await axios
        .get(config.path_api + "/billsale/billSaleDetailInfo", config.headers())
        .then((res) => {
          if (res.data.result != null) {
            setCurrentBill(res.data.result);
            sumTotalPirce(res.data.result.billsaledetails);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const sumTotalPirce = (billsaledetails) => {
    let sum = 0;
    for (let i = 0; i < billsaledetails.length; i++) {
      const item = billsaledetails[i];
      const qty = parseInt(item.qty);
      const price = parseInt(item.price);

      sum += qty * price;
    }
    setTotalPrice(sum);
  };

  const sumTotalPirceLastBill = (billsaledetails) => {
    let sum = 0;
    for (let i = 0; i < billsaledetails.length; i++) {
      const item = billsaledetails[i];
      const qty = parseInt(item.qty);
      const price = parseInt(item.price);

      sum += qty * price;
    }
    setTotalPriceLastBill(sum);
  };

  const sumTotalPircePreviewBill = (item) => {
    let sum = 0;
    for (let i = 0; i < item.billsaledetails.length; i++) {
      const items = item.billsaledetails[i];
      const qty = parseInt(items.qty);
      const price = parseInt(items.price);

      sum += qty * price;
    }
    setTotalPricePreviewBillSale(sum);
  };

  const handleDeleteItem = (item) => {
    try {
      Swal.fire({
        title: "Confirm delete",
        text: "Confirm delete list pruduct",
        icon: "question",
        showCancelButton: true,
        showConfirmButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          await axios
            .delete(
              config.path_api + "/billsale/deleteItem/" + item.id,
              config.headers()
            )
            .catch((err) => {
              throw err.response.data;
            })
            .then((res) => {
              if (res.data.message === "success") {
                Swal.fire({
                  title: "Delete",
                  text: "Delete item product",
                  icon: "success",
                  timer: 2000,
                });
                fetchDateSale();
              }
            });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleUpdateQty = async () => {
    try {
      await axios
        .post(config.path_api + "/billsale/updateQty", item, config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            const btn = document.getElementsByClassName("btnClose");
            for (let i = 0; i < btn.length; i++) btn[i].click();
            fetchDateSale();
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleEndSale = (e) => {
    e.preventDefault();
    try {
      Swal.fire({
        title: "End of sale",
        text: "Confirm end of sale",
        icon: "question",
        showConfirmButton: true,
        showCancelButton: true,
      }).then(async (res) => {
        if (res.isConfirmed) {
          await axios
            .get(config.path_api + "/billsale/billendsale", config.headers())
            .then((res) => {
              if (res.data.message === "success") {
                Swal.fire({
                  title: "Successfully",
                  text: "Confirm end of s ale success",
                  icon: "success",
                  timer: 2000,
                });
                setCurrentBill({});
                setTotalPrice(0);
                openBillSale();
                fetchDateSale();
                const btn = document.getElementsByClassName("btnClose");
                for (let i = 0; i < btn.length; i++) btn[i].click();
                if (saleRef.current) {
                  saleRef.current.refreshCountBill();
                }
              }
            })
            .catch((err) => {
              throw err.response.data;
            });
        }
      });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleLastBill = async () => {
    try {
      await axios
        .get(config.path_api + "/billsale/lastBill", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setLastBill(res.data.result[0]);
            sumTotalPirceLastBill(res.data.result[0].billsaledetails);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handleBillToday = async () => {
    try {
      await axios
        .get(config.path_api + "/billsale/billToday", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setBillToday(res.data.results);
          }
        })
        .catch((err) => {
          throw err.response.data;
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  const handlePrintSilp = async () => {
    try {
      await axios
        .get(config.path_api + "/member/info", config.headers())
        .then((res) => {
          if (res.data.message === "success") {
            setMemberInfo(res.data.result);
            handleLastBill();
          }
        });
    } catch (e) {
      Swal.fire({
        title: "Error",
        text: e.message,
        icon: "error",
      });
    }
  };

  return (
    <>
      <Template ref={saleRef}>
        <div className="card">
          <div className="card-header">
            <i className="fa fa-person mr-2"></i>
            Sale Product
            <div className="float-right">
              <button
                data-bs-toggle="modal"
                data-bs-target="#modalEndSale"
                className="btn btn-success me-2"
              >
                <i className="fa fa-check me-2"></i>
                End of sale
              </button>
              <button
                onClick={handleBillToday}
                data-bs-toggle="modal"
                data-bs-target="#modalBillToday"
                className="btn btn-info me-2"
              >
                <i className="fa fa-file me-2"></i>
                Bill today
              </button>
              <button
                onClick={handleLastBill}
                data-bs-toggle="modal"
                data-bs-target="#modalLastBill"
                className="btn btn-secondary me-2"
              >
                <i className="fa fa-file-alt me-2"></i>
                Latest bill
              </button>

              <button onClick={handlePrintSilp} className="btn btn-primary">
                <i className="fa fa-print me-2"></i>
                พิมพ์บิล
              </button>
            </div>
          </div>
          <div className="card-body">
            <div className="row justify-content-end ">
              <div className="col-lg-9">
                <div className="row g-2">
                  {product.length > 0
                    ? product.map((item) => (
                        <div key={item.id} className="col-lg-3">
                          <div className="card h-100">
                            <div className="card-body">
                              <img
                                className="card-img-top"
                                src={
                                  config.path_api +
                                  "/upload/" +
                                  item.productimages[0].imageName
                                }
                                alt=""
                              />
                            </div>
                            <div className="card-footer text-center">
                              <div className="text-primary h5">{item.name}</div>
                              <div className="h5">
                                {parseInt(item.price).toLocaleString("th-TH")}
                              </div>
                              <button
                                onClick={(e) => handleSave(item)}
                                className="btn btn-primary w-100"
                              >
                                <i className="fa fa-cart-arrow-down me-2"></i>
                                Buy
                              </button>
                            </div>
                          </div>
                        </div>
                      ))
                    : ""}
                </div>
              </div>
              <div className="col-lg-3">
                <div className="bg-dark rounded">
                  <h1 className="text-end text-success me-2">
                    {totalPirce > 0
                      ? parseInt(totalPirce).toLocaleString("th-TH")
                      : "0"}{" "}
                    .-
                  </h1>
                </div>
                {currentBill.billsaledetails !== undefined &&
                currentBill.billsaledetails.length > 0
                  ? currentBill.billsaledetails.map((item) => (
                      <div key={item.id} className="card">
                        <div className="card-body">
                          <div className=" text-primary fw-bold h5">
                            {item.product.name}
                          </div>
                          <div className="">
                            <span className="h4 text-danger">{item.qty}</span> x{" "}
                            {parseInt(item.price).toLocaleString("th-TH")} ={" "}
                            {parseInt(item.qty * item.price).toLocaleString(
                              "th-TH"
                            )}
                            <div className=" row g-2 mt-3">
                              <div className="col-lg-6">
                                <button
                                  onClick={(e) => setItem(item)}
                                  data-bs-toggle="modal"
                                  data-bs-target="#modalQty"
                                  className="btn btn-primary w-100"
                                >
                                  <i className="fa fa-pencil"></i>
                                </button>
                              </div>
                              <div className="col-lg-6">
                                <button
                                  onClick={(e) => handleDeleteItem(item)}
                                  className="btn btn-danger w-100"
                                >
                                  <i className="fa fa-times"></i>
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))
                  : ""}
              </div>
            </div>
          </div>
        </div>
      </Template>

      <Modal id="modalQty" title="Edit Amount" modalSize="modal-md">
        <div className="">
          <label htmlFor="" className="form-label">
            Amount
          </label>
          <input
            value={item.qty}
            onChange={(e) => setItem({ ...item, qty: e.target.value })}
            type="text"
            className="form-control"
          />
        </div>
        <div className="mt-2">
          <button onClick={handleUpdateQty} className="btn btn-primary">
            <i className="fa fa-pencil me-2"></i>
            Save edit amount
          </button>
        </div>
      </Modal>

      <Modal id="modalEndSale" title="End of sale" modalSize="modal-sm">
        <form onSubmit={handleEndSale} className="row g-2">
          <div className="col-lg-12">
            <label className="form-label">Total</label>
            <input
              value={totalPirce.toLocaleString("th-TH")}
              type="text"
              className="form-control text-end"
              disabled
            />
          </div>
          <div className="col-lg-12">
            <label className="form-label">Paid amount</label>
            <input
              value={inputMoney.toLocaleString("th-TH")}
              onChange={(e) => setInputMoney(e.target.value)}
              className="form-control text-end"
            />
          </div>
          <div className="col-lg-12">
            <label className="form-label">Money withdraw</label>
            <input
              value={(inputMoney - totalPirce).toLocaleString("th-TH")}
              className="form-control text-end"
              disabled
            />
          </div>
          <div className="col-lg-12 mt-3">
            <button
              onClick={(e) => setInputMoney(totalPirce)}
              type="button"
              className="btn btn-primary me-2"
            >
              <i className="fa fa-check me-2"></i>
              Pay in full
            </button>
            <button
              onClick={handleEndSale}
              type="submit"
              className="btn btn-success"
            >
              <i className="fa fa-check me-2"></i>
              End of sale
            </button>
          </div>
        </form>
      </Modal>

      <Modal id="modalBillToday" title="Bill today" modalSize="modal-lg">
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th style={{ width: 200 }}>Pirview</th>
              <th style={{ width: 50 }}>Id</th>
              <th>Created at</th>
            </tr>
          </thead>
          <tbody>
            {billToday.length > 0
              ? billToday.map((item) => (
                  <tr key={item.id}>
                    <td className="text-center">
                      <button
                        onClick={(e) => {
                          setPreviewBillSale(item);
                          sumTotalPircePreviewBill(item);
                        }}
                        data-toggle="modal"
                        data-target="#modalPreviewBillSale"
                        className="btn btn-primary"
                      >
                        <i className="fa fa-eye me-2"></i>
                        View details
                      </button>
                    </td>
                    <td>{item.id}</td>
                    <td>
                      {dayJs(item.createdAt).format("DD/MM/YYYY HH:mm:ss")}
                    </td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
      </Modal>

      <Modal id="modalLastBill" title="Last bill" modalSize="modal-lg">
        <button></button>
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>No.</th>
              <th>Barcode</th>
              <th>Name</th>
              <th className="text-end">Amount</th>
              <th className="text-end">Price</th>
              <th className="text-end">Total</th>
            </tr>
          </thead>
          <tbody>
            {lastbill.billsaledetails !== undefined
              ? lastbill.billsaledetails.map((item, index) => (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{item.product.barcode}</td>
                    <td>{item.product.name}</td>
                    <td className="text-end">{item.qty}</td>
                    <td className="text-end">
                      {parseInt(item.price).toLocaleString("th-TH")}
                    </td>
                    <td className="text-end">
                      {parseInt(item.qty * item.price).toLocaleString("th-TH")}
                    </td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
        <p className="text-end">
          Total price: {totalPirceLastBill.toLocaleString("th-TH") + " Bath"}
        </p>
      </Modal>

      <Modal
        id="modalPreviewBillSale"
        title={"Preview bill sale"}
        modalSize="modal-lg"
      >
        <table className="table table-bordered table-striped">
          <thead>
            <tr>
              <th>No.</th>
              <th>Barcode</th>
              <th>Name</th>
              <th className="text-end">Amount</th>
              <th className="text-end">Price</th>
              <th className="text-end">Total</th>
            </tr>
          </thead>
          <tbody>
            {preViewBillSale.billsaledetails !== undefined &&
            preViewBillSale.billsaledetails.length > 0
              ? preViewBillSale.billsaledetails.map((item, index) => (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{item.product.barcode}</td>
                    <td>{item.product.name}</td>
                    <td>{item.qty}</td>
                    <td>{parseInt(item.price).toLocaleString("th-TH")}</td>
                    <td>
                      {parseInt(item.qty * item.price).toLocaleString("th-TH")}
                    </td>
                  </tr>
                ))
              : null}
          </tbody>
        </table>
        <p className="text-end">
          Total price:{" "}
          {totalPircePreviewBillSale.toLocaleString("th-TH") + " Bath"}
        </p>
      </Modal>

      <div id="slip">
        <div>เลขที่บิล: {lastbill.id}</div>
        <center>[LOGO]</center>
        <center>ใบเสร็จรับเงิน</center>
        <center>
          <strong>{memberinfo.name}</strong>
        </center>

        <hr />

        <table width="100%">
          {memberinfo != {} ? (
            <>
              <thead>
                <>
                  <tr>
                    <th align="left">สินค้า</th>
                    <th align="center">จำนวน</th>
                    <th align="right">ราคา</th>
                    <th align="right">Total</th>
                  </tr>
                </>
              </thead>
              <tbody>
                {lastbill != {} &&
                lastbill.billsaledetails != undefined &&
                lastbill.billsaledetails.length > 0
                  ? lastbill.billsaledetails.map((item) => (
                      <>
                        <tr>
                          <td align="left">{item.product.name}</td>
                          <td align="center">{item.qty}</td>
                          <td align="right">
                            {parseInt(item.price).toLocaleString("th-TH")}
                          </td>
                          <td align="right">
                            {parseInt(item.qty * item.price).toLocaleString(
                              "th-TH"
                            )}
                          </td>
                        </tr>
                      </>
                    ))
                  : null}
              </tbody>
            </>
          ) : null}
        </table>

        <hr />

        <div >Total: {totalPirceLastBill.toLocaleString("th-TH")} bath</div>
        <div>{dayjs(lastbill.createdAt).format("DD/MM/YYYY HH:mm:ss")}</div>
      </div>
    </>
  );
}

export default Sale;
